<template>
  <div>
    <div class="title">漫画 : {{$route.query.n}}</div>
    <el-table :data="tableData"style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left">
            <el-form-item label="预览">
              <img :src="props.row.content" width="800"/>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column label="序号">
          <template slot-scope="scope">
            <a>第{{scope.row.chapter}}章</a>
          </template>
      </el-table-column>
      <el-table-column label="章节" prop="title">
      </el-table-column>
      <el-table-column label="状态" align="center">
        <template slot-scope="scope" >
          <el-tag color='#F0F8FF' type="info" v-if="scope.row.state">通过</el-tag>
          <el-tag color='#FFF5EE' type="info" v-if="!scope.row.state">待审</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="发布时间" width="156" getLocalTime :formatter="dateFormat" prop="date">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-popover ref="popover{{$index}}" class="aew" placement="top" >
            <p>删除 {{ scope.row.title }} ？</p>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="cancel()">取消</el-button>
              <el-button size="mini" type="danger" round @click="handleDelete(scope.$index, scope.row)">确定</el-button>
            </div>
          </el-popover>
          <el-button v-if="!scope.row.state" size="mini" type="danger" v-popover:popover{{$index}} ><i class="el-icon-delete"/></el-button>
          <el-button size="mini" v-if="!scope.row.state" @click="access(scope.row.sid)">通过</el-button>
          <p v-else><el-tag color='#F0F8FF' type="info">已更新</el-tag></p>
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination layout="prev, pager, next" :current-page="currentPage" :total=totalCount @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      searchParam: {},
      currentPage: 1,
      totalCount: 0,
      tableData: []
    }
  },
  created() {
    this.searchParam.param = this.$route.query.cid
    this.getData()
  },
  methods: {
    access(id) {
      var _this = this
      this.$ajax.post('/api/accessSection?id='+id).then(function(response){
        _this.getData()
      }).catch(function(error){console.log(error)})
    },
    handleCurrentChange(val) {
      this.pageParam.page = val-1
      this.currentPage = val
      this.getData()
    },
    handleDelete(index, row) {
      document.querySelector("#app").click()
      var _this = this
      this.$ajax.post('/api/deleteSection?id='+row.sid).then(function(response){
        if(response.data.code === 0) 
        _this.$message({message: response.data.data,type: 'success'});
        _this.getData()
      }).catch(function(error){console.log(error)})
    },
    dateFormat(row, column) {  
      return this.getDateTime(row[column.property])
    },
    getData() {
      var _this = this
      this.$ajax.post('/api/getSectionByFlag',_this.searchParam).then(function(response){
        _this.tableData = response.data.data.content
        _this.totalCount = response.data.data.totalElements
        console.log(response.data)
      }).catch(function(error){console.log(error)})
    },
    cancel() {
      document.querySelector("#app").click()
    },
    goLink(index, row) {
      this.setArticle(row)
      this.$router.push({ 
        path: '/ArticleW',
        query:{status:0}
      })
    }
  }
}
</script>

<style lang='stylus' rle='stylesheet/stylus'>
.title
  font-size 20px
a
  text-decoration none
  color #5CACEE
a:hover
  cursor pointer
.head
  font-size 18px
  margin-bottom 14px
.el-tag--info
  border-color #fff
.el-pagination .btn-next, .el-pagination .btn-prev, .el-pagination
  color #909399
.el-pager li.active
  color	#FFB6C1
.pager
  margin 35px
  text-align center
</style>



